/* global ymaps*/
import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {Form} from './modules/form-validate/form';

import './modules/header/locomotive-scroll';
import {Burger} from './modules/header/burger';
import {StickyHeader} from './modules/header/sticky-header';
import {initLocomotiveScroll} from './modules/header/init-locomotive-scroll';
import {init} from './modules/modals/map';

window.addEventListener('DOMContentLoaded', () => {
  initLocomotiveScroll();

  if (document.getElementsByClassName('main-header--no-js').length !== 0) {
    let navMenu = document.querySelector('.main-header');
    navMenu.classList.remove('main-header--no-js');
  }
  // ---------------------------------
  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    const form = new Form();
    window.form = form;
    form.init();
    const burger = new Burger();
    burger.init();
    const stickyHeader = new StickyHeader();
    stickyHeader.init();

    if (document.getElementById('map')) {
      ymaps.ready(init);
    }
  });
});

